@import url(https://fonts.googleapis.com/css2?family=Mountains+of+Christmas&display=swap);
* {
  box-sizing: border-box;
}

html {
  height: 100%
}

body {
  margin: 0;
  font-family: 'Mountains of Christmas';
  height: 100%;
}

.App {
  display: flex;
  background-color: rgb(51, 173, 255);
  text-align: center;
  flex-direction: column;
  min-height: 100vh;
}

.App-intro {
  background-color: rgb(9, 145, 66);
  text-align: center;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: auto;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 10s linear;
            animation: App-logo-spin infinite 10s linear;
  }
}

.App-body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 3vmin);
  color: white;
  min-height: 65vh;
}

.App-body-intro {
  background-color: rgb(9, 145, 66);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 3vmin);
  color: white;
  min-height: 70vh;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

button, .button {
  background-color: #ed4029;
  padding: 8px 40px;
  font-family: 'Mountains of Christmas';
  color: whitesmoke;
  border-radius: 10px;
  border: none;
  box-shadow: 0 8px 16px 0 #722015, 0 6px 20px 0 #722015;
  font-size: calc(10px + 2vmin);
  letter-spacing: 2px;
  margin: 10px;
}

.elfNameInput {
  height: 30px;
}


.footer {
  background-image: url("/Christmas/static/media/Snow background.10f0f9c6.png");
  background-position: center top;
  background-size: cover;
  min-height: 35vh;
  z-index: 2;
  overflow: visible;
  max-width: 100vw;
}

input[type=text] {
  border: none;
  padding: 2px;
  height: calc(10px + 2vmin);
  top: 0;
  text-align: center;
}

h2 {
  margin: 0;
  line-height: 2.2;
  font-size: calc(12px + 3vmin);
  color: white;
}

h3 {
  margin: 0;
  line-height: 1;
  font-size: calc(12px + 2vmin);
  color: white;
}

h1 {
  color: white;
}

.game {
  background-color: rgb(51, 173, 255);
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  text-align: center;
}

.header {
  height: 100vh;
  margin-top: 30vh;
}

.game-board {
  height: 100%;
  background-color: white;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

@-webkit-keyframes header-reduce {
  from{ margin-top: 30vh; height: 100vh;}
  to{ margin-top: 0vh; height: 0vh;}
}

@keyframes header-reduce {
  from{ margin-top: 30vh; height: 100vh;}
  to{ margin-top: 0vh; height: 0vh;}
}

@-webkit-keyframes header-move {
  from { transform: traslateY(0px); }
  to { transform: traslateY(-100px); }
}

@keyframes header-move {
  from { transform: traslateY(0px); }
  to { transform: traslateY(-100px); }
}

.snow-image {
  max-height: 40vh;
  overflow: visible;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  margin-top: -10vh;
}

.snow-image img {
  height: 100%;
  width: auto;
  margin: 0;
  object-fit: cover;
  top: -100px;
}

.game-start {
  display: flex;
  height: 100vh;
  width: 100vw;
  background-color: rgb(51, 173, 255);
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
}

h1.game-complete {
  color: black;
  font-size: 2rem;
}
body {
    margin: 0;
  }
  
  .grid-container {
    display: grid;
    grid-gap: 10px;
    grid-template-columns: auto auto auto auto;
    padding: 10px 10px 10px 10px;
  }

  
  .grid-item {
    border: 1px solid rgba(0, 0, 0, 0.8);
  }
  
  .justify-left {
    text-align: left
  }
  
  .justify-end {
    text-align: right;
  }
  
  .justify-center {
    text-align: center;
  }
  
  .card {
    display: flex;
    text-align: center;
    justify-content: center;
    width: 8.5em;
    height: 10em;
    color: white;
    font-size: 15px;
  }
  
  .hide-card {
    visibility: hidden;
  }
  
  .card-front {
    background-color: #1c5c21;
    box-shadow: 5px 5px 5px rgba(68, 68, 68, 0.6);
  }
  
  .card-back {
    font-weight: bold;
  }

  .image-holder {
      width: 100%;
      height: 100%;
      background-image: url("/Christmas/static/media/Bow and snow.4b571af6.png");
      background-position: center;
      background-size: cover;
      pointer-events: none;
  }

  img {
      max-width: 100%;
      max-height: 100%;
      margin:0.5em;
  }


