body {
    margin: 0;
  }
  
  .grid-container {
    display: grid;
    grid-gap: 10px;
    grid-template-columns: auto auto auto auto;
    padding: 10px 10px 10px 10px;
  }

  
  .grid-item {
    border: 1px solid rgba(0, 0, 0, 0.8);
  }
  
  .justify-left {
    text-align: left
  }
  
  .justify-end {
    text-align: right;
  }
  
  .justify-center {
    text-align: center;
  }
  
  .card {
    display: flex;
    text-align: center;
    justify-content: center;
    width: 8.5em;
    height: 10em;
    color: white;
    font-size: 15px;
  }
  
  .hide-card {
    visibility: hidden;
  }
  
  .card-front {
    background-color: #1c5c21;
    box-shadow: 5px 5px 5px rgba(68, 68, 68, 0.6);
  }
  
  .card-back {
    font-weight: bold;
  }

  .image-holder {
      width: 100%;
      height: 100%;
      background-image: url('./resources/Bow\ and\ snow.png');
      background-position: center;
      background-size: cover;
      pointer-events: none;
  }

  img {
      max-width: 100%;
      max-height: 100%;
      margin:0.5em;
  }

